
import { setAttribute } from '../../virtual-dom'
import { delay } from '../../utils'
import { ContainerComponent } from './container'
import { Component, html, IComponent } from '../utils'

@Component(
    'player-pointer',
    html`<div class="player-pointer">
        <img
            src="data:image/png;base64,"
            alt="pointer"
        />
        <div class="spinner"></div>
    </div>`
)
export class PointerComponent implements IComponent {
    public parent: HTMLElement
    public target: HTMLElement

    private x = 0
    private y = 0

    private show: boolean
    private pointer: HTMLElement
    private c: ContainerComponent
    constructor(c: ContainerComponent) {
        this.c = c
        this.initPointer()
        this.togglePointer(true)
    }

    private initPointer() {
        this.pointer = this.c.container.querySelector('.player-pointer') as HTMLElement
    }

    private togglePointer(show = !this.show) {
        this.show = show
        ;(this.pointer.firstElementChild as HTMLElement).style.display = this.show ? 'inherit' : 'none'
    }

    public hidePointer() {
        this.togglePointer((this.show = false))
    }

    public move(x: number, y: number) {
        this.x = x
        this.y = y
        this.pointer.style.left = this.x + 'px'
        this.pointer.style.top = this.y + 'px'
    }

    public async click(x: number, y: number) {
        this.move(x, y)
        if (this.pointer.hasAttribute('active')) {
            return
        }
        await delay(200)
        setAttribute(this.pointer, 'active', '')
        await delay(400)
        setAttribute(this.pointer, 'active', null)
    }
}
